<template>
	<view>

			<uni-forms ref="form" :modelValue="form" :rules="rules">
				
					<uni-forms-item name="roomName">
							<uni-easyinput  v-model="form.roomName" prefixIcon=""
							placeholder="请输入直播间名字"> </uni-easyinput>
					</uni-forms-item>
				
					<uni-forms-item  name="roomContent">
						<uni-easyinput type="textarea" autoHeight=""  v-model="form.roomContent" prefixIcon=""
						placeholder="请输入简介"> </uni-easyinput>
					</uni-forms-item>
			</uni-forms>	<!-- 动态绑定地址 -->
		
        
		<live-pusher id='livePusher' ref="livePusher" class="livePusher" 
		style="width: 750rpx;" :style="{height:app_height-180+'px'}"
		:url="pushUrl" 
        mode="SD" :muted="true" :enable-camera="true" :auto-focus="true" :beauty="1"
		 whiteness="2"
        aspect="9:16" @statechange="statechange" @netstatus="netstatus" @error = "error"
         >
		 <cover-view style="background-color: ;top: 0%;height: 120rpx;">
			 {{form.roomName}}
		 </cover-view>
		 </live-pusher>
		
        <uni-icons type="camera-filled" size="30" @click="start"></uni-icons>
		<text>开始直播</text>

		<uni-icons type="minus" size="30" @click="stop"></uni-icons>
		<text>结束直播</text>
        <uni-icons type="camera" size="30" @click="switchCamera"></uni-icons>
		<text>翻转相机</text>
	
		<button @click="get">获取数据</button>
		
		
    </view>
</template>
<script>
    export default {
        data() {
			return {
				app_height:0,
				//推流地址
				pushUrl: "",
				//房间id号
				roomId: "",
				//用户名id号的别称
				masterId:"",
										
				//视频的宽高
				pusherClass:{
					width:'750rpx',
					
				},
				form:{"roomName":'',
				"roomContent":'',
				"roomType":'',
				"startTime":'2022-11-06 02:37:06',
				"roomImage":''},
				rules:{
					//对name字段进行必填验证
					roomName:{
						rules:[{
							required:true,
							errorMessage:'请输入直播间名字'
						}],
						label:'姓名'
						,
						validateTrigger:'bind'
					},
					roomContent:{
						rules:[{
							required:true,
							errorMessage:'请输入简介'
						}],
						label:'密码'
						,
						validateTrigger:'bind'
					}
				},
				
			}
        },
		
		onLoad() {
			let res=uni.getSystemInfoSync()
				this.statusBarHeight=res.statusBarHeight
				this.app_height=res.windowHeight
			//微信小程序可能需要打开摄像头权限，这是打开的函数
			    // const _this = this
			    // let promise = new Promise((resolve, reject) => {
			    //   wx.showModal({
			    //     title: '授权',
			    //     content: '请先授权获取摄像头权限',
			    //     success(res) {
			    //       if (res.confirm) {
			    //         uni.openSetting({
			    //           success(res) {
			    //             if (res.authSetting['scope.camera']) { // 用户打开了授权开关
			    //               resolve(true)
			    //             } else { // 用户没有打开授权开关， 继续打开设置页面
			    //               _this.openSetting().then(res => {
			    //                 resolve(true)
			    //               })
			    //             }
			    //           },
			    //           fail(res) {
			    //             console.log(res)
			    //           }
			    //         })
			    //       } else if (res.cancel) {
			    //         _this.openSetting().then(res => {
			    //           resolve(true)
			    //         })
			    //       }
			    //     }
			    //   })
			    // })
			    // return promise;
			 

		},
		//页面每一次加载的时候都获取存储的房间信息
		onShow() {
			let push = uni.getStorageSync('pushUrl');
			
			//问题可能出在这里，data里的推流地址没被设置成功？或者是组件不支持动态绑定？
			this.pushUrl = push.pushUrl+"/"+push.key;
			uni.setStorageSync('push',this.pushUrl)
			console.log(this.pushUrl)
			// this.pushUrl="rtmp://175.178.85.36/live/1589100171841552385";
			// this.pushUrl = pushUrl;
			// this.roomId = user.roomId;
			
		},
        onReady() {
            // 注意：需要在onReady中 或 onLoad 延时
            this.context = uni.createLivePusherContext("livePusher", this);
        },
        methods: {
			get(){
				const _this=this;
				let baseUrl="http://175.178.85.36:10010";
				uni.request({
					url:baseUrl+'/user/getUserMessage',
					method:"GET",
					header:{authentication:uni.getStorageSync('authentication')?uni.getStorageSync('authentication'):''},
					success: (res) => {
						
				
							uni.setStorageSync('userMsg',res.data.data)
							console.log("storage usermsg success")
							
						
					}
				})
				uni.request({
					url:baseUrl+'/user/getKeyAndWord',
					method:"GET",
					header:{authentication:uni.getStorageSync('authentication')?uni.getStorageSync('authentication'):''},
					success: (res) => {
						
							uni.setStorageSync('pushUrl',res.data.data);
							
							_this.pushUrl=res.data.data.pushUrl+"/"+res.data.data.key;
							
							console.log(_this.pushUrl)
							console.log(res);
							console.log("storage success")
						
					}
				});
				
			}
			,
			startPush(){
				let _form=this.form;
				this.start();
				this.$refs.form.validate().then(res=>{
								
					//确认用户的登录状态
					this.request({url:'/user/keepLogin',method:'GET',data:this.form
					})
					.then(con=>{
						if(con.code===1){
							
							//发起直播的请求，获得后端的数据
							this.request({url:'/liveRoom/createRoom',method:'POST',
							data:this.form,})
							.then(res=>{
								if(res.code===1){
									this.start();
									uni.showToast({						
										title:'创建成功'
									})
									//存储用户的数据到storage
									uni.setStorageSync(roomName,res.data)	
									//跳转页面
									// uni.switchTab({
									// 	url:"/pages/tabbar/tabbar-1/tabbar-1"
									// })
								};
								
							}).catch(err=>{
								console.log("表单数据信息",err);
							})
						}
					 })
				})
			},
			closeRoom(){
				let _this = this;
				let user=uni.getStorageSync('user');
				let roomId = user.roomId;
				
				this.request({url:'/liveRoom/deleteRoom?roomId='+roomId, method:'DELETE', data:roomId})
				.then(res => {
					if(res.code == 1){
						_this.close();
						// uni.clearStorage('user');
						console.log(res);
						uni.showToast({
							title:'删除成功'
						})
					}
				});
			}
			,

            statechange(e) {
                console.log("statechange:" + JSON.stringify(e));
            },
            netstatus(e) {
				
                console.log("netstatus:" + JSON.stringify(e));
            },
            error(e) {
                console.log("error:" + JSON.stringify(e));
            },
            start: function() {
				
			//开始直播，
			this.context.start({
			    success: (a) => {
			        console.log("livePusher.start:" + JSON.stringify(a));
			    }
			});
            },
            close: function() {
				
				//结束直播
				this.context.close({
				                    success: (a) => {
				                        console.log("livePusher.close:" + JSON.stringify(a));
				                    }
				                });
            },
            snapshot: function() {
                this.context.snapshot({
                    success: (e) => {
                        console.log(JSON.stringify(e));
                    }
                });
            },
            resume: function() {
                this.context.resume({
                    success: (a) => {
                        console.log("livePusher.resume:" + JSON.stringify(a));
                    }
                });
            },
            pause: function() {
                this.context.pause({
                    success: (a) => {
                        console.log("livePusher.pause:" + JSON.stringify(a));
                    }
                });
            },
            stop: function() {
                this.context.stop({
                    success: (a) => {
                        console.log(JSON.stringify(a));
                    }
                });
            },
            switchCamera: function() {
                this.context.switchCamera({
                    success: (a) => {
                        console.log("livePusher.switchCamera:" + JSON.stringify(a));
                    }
                });
            },
            startPreview: function() {
                this.context.startPreview({
                    success: (a) => {
                        console.log("livePusher.startPreview:" + JSON.stringify(a));
                    }
                });
            },
            stopPreview: function() {
                this.context.stopPreview({
                    success: (a) => {
                        console.log("livePusher.stopPreview:" + JSON.stringify(a));
                    }
                });
            }
        }
    }
</script>

